.face{
  margin-top: $base-padding*3;
  width: 160px;
  height: 220px;
  width: 80px;
  height: 220px;
  object-fit: cover;
  object-position: left;
  transition: width $base-animation-duration;
  &:hover{
    width: 160px;
  }
  @include media($screen) {
    width: 160px;
  }
  @include media($mobile) {
    margin-top: $base-padding*1;
    width: 107px;
    height: 147px;
    &:hover{
      width: 107px;
    }
  }
}

.two-face__right{
  .face{
    object-position: right;
  }
}
